import React, { useEffect } from "react"; 
import { withRouter } from "react-router";
import { useDispatch, useSelector } from "react-redux";
import FileUploader from "Component/FileUploader";
import ReactExport from "react-export-excel";
import { getWarehouses } from "apiServices";
import "./index.scss";

const ExcelFile = ReactExport.ExcelFile;
const ExcelSheet = ReactExport.ExcelFile.ExcelSheet;
const ExcelColumn = ReactExport.ExcelFile.ExcelColumn;

const Upload = (props) => { 
  const dispatch = useDispatch();
  const warehousesData = useSelector((state) => state.warehouseData);
  const Field = [
    {
      dataField: "id", //field ini wajib ada untuk editable column
      text: "ID",
      editable: false,
      sort: true,
      hidden: true,
      type: "number",
    },
    {
      dataField: "warehouseDestination",
      text: "Warehouse",
      type: "string",
      sort: true,
    }, 
    {
      dataField: "wmsPoSkuCode",
      text: "PO SKU Code",
      type: "number",
      sort: true,
    },
    {
      dataField: "skuNumber",
      text: "SKU Number",
      type: "number",
      sort: true,
    },
    {
      dataField: "skuDescription",
      text: "SKU Description",
      type: "text",
      sort: true,
    },
    // {
    //   dataField: "uom",
    //   text: "UOM",
    //   type: "text",
    //   sort: true,
    // },   
    {
      dataField: "category",
      text: "Category",
      type: "text",
      sort: true,
    },   
    {
      dataField: "expiryDateCol",
      text: "Expiry Date",
      type: "date_indo",
      sort: true,
    },
    {
      dataField: "qty",
      text: "Qty",
      type: "number",
      sort: true,
    },
  ];

  const dataSet1 = [
    {
      data: "", 
    },
  ];

  useEffect(() => {
    if (!warehousesData) {
      getWarehouses({ dispatch });
    }
  }, []);

  return (
    <div className="mb-5">
      {/* export this table to excel */}
      <ExcelFile
        element={
          //dont change id and style please
          <button id="download-template-xls" style={{ display: "none" }}>
            Download Data
          </button>
        }
        //file name without types
        filename="AdditionalInboundUpload_Template"
      >
        <ExcelSheet data={dataSet1} name="template">
          <ExcelColumn label="Warehouse" value="data" /> 
          <ExcelColumn label="PO SKU Code" value="data" /> 
          <ExcelColumn label="SKU Number" value="data" />
          <ExcelColumn label="SKU Description" value="data" />
          {/* <ExcelColumn label="UOM" value="data" />  */}
          <ExcelColumn label="Category" value="data" /> 
          <ExcelColumn label="Expiry Date" value="data" /> 
          <ExcelColumn label="Qty" value="data" /> 
        </ExcelSheet>
      </ExcelFile>
      {/* end export this table to excel */}

      <FileUploader
        nextPage={() =>
          props.history.push("/inventory-entry/additional-inbound/CSV-upload/review")
        }
        transaction={"additional_inbound"}
        field={Field}
      />
    </div>
  );
};

export default withRouter(Upload);
